#dashContainer {
    background: var(--color_background);
}

#dashboardBox {
    margin-top: -30px;
}

.dashCompDiv {
    border: 3px solid var(--color_background);
    border-radius: 25px;
    padding: 10px 30px;
    text-align: center;
    background: white;
}

#deviceConnect {
    width: 500px;
    margin-left: auto;
    margin-right: auto;
    background: white;
    border-radius: 25px;
    padding: 10px 30px;
}

#door{
    height: 150px;
}

#lightbulb {
    width: 50px;
}

#colorSel {
    width: 80%;
    height: 150px;
    margin-left: auto;
    margin-right: auto;
}

#lightScheduleDiv {
    margin-top: 25px;
}

.timeSel {
    margin: 15px;
}

#tempEntry {
    width: 75px;
    display: block;
}

#currentTempData {
    margin-left: auto;
    margin-right: auto;
    color: var(--color_background);
    width: 150px;
}

#curTemp {
    font-size: 40px;
    margin: -5px;
}

#timeDisp {
    font: 30px Arial, sans-serif;
    color: white;
    margin-top: 20px;
    background: var(--color_background);
    text-align: center;
}